.form {
    width: 300px;
    display: flex;
    flex-direction: column;
    gap: 16px;
    justify-content: flex-start;
    margin-bottom: 40px;
    margin-top: 40px;
    margin-left: auto;
    margin-right: auto;
  }
  
  .label {
    display: block;
    text-align: left;
    font-size: 16px;
    line-height: 1.5;
    letter-spacing: 0.02em;
  }
  
  .input {
    display: block;
    font-weight: 300;
    font-size: 18px;
    line-height: 1.5;
    letter-spacing: 0.02em;
    outline: none;
    width: 100%;
    padding: 0 12px;
    height: 42px;
    background: rgba(255, 255, 255, 0.05);
    border: 1px solid rgba(255, 255, 255, 0.89);
    backdrop-filter: blur(2px);
    border-radius: 2px;
  
    transform: border cubic-bezier(0.4, 0, 0.2, 1) 250ms;
  }
  
  .input:hover {
    border: 1px solid #07040b;
}
  
  .button {
    color: white;
    font-weight: 400;
    font-size: 18px;
    line-height: 1.2;
    text-align: center;
    letter-spacing: 0.05em;
    padding: 8px;
    border-radius: 2px;
    height: 42px;
    border: none;
    background: linear-gradient(
      270deg,
      #2f37a3 -20.93%,
      #752ae6 116.86%,
      #a976f5 116.86%
    );
    transform: background cubic-bezier(0.4, 0, 0.2, 1) 250ms;
  }
  
  .button:hover {
    background: linear-gradient(
      270deg,
      #6421c9 -20.93%,
      #752ae6 116.86%,
      #975feb 116.86%
    );
  }
  